
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td{margin:0;padding:0;border:0;outline:0;font-weight:inherit;font-style:inherit;font-family:inherit;font-size:100%;-webkit-tap-highlight-color:rgba(255,255,255,0);vertical-align:baseline;font-family:Arial,Helvetica,sans-serif;text-decoration:none;color:#666
}
body{overflow-y:scroll
}
.wraper{width:90%;margin:auto
}
.wraper .item{float:left;display:inline-block;width:35%;min-height:500px;margin-left:10%;margin-bottom:10px
}
.wraper .title{line-height:40px;font-weight:bold;color:#666
}
.wraper h3{display:inline-block;line-height:35px
}
.wraper p{display:inline-block;line-height:30px
}
.wraper a.url{text-shadow:1px 2px 3px red
}
@media screen and (max-width: 1400px){
.wraper{width:1100px
}
.wraper .item{margin-left:30px;width:520px
}
}
@media screen and (max-width: 1150px){
.wraper{position:relative;width:100%;height:auto
}
.wraper .item{position:relative;display:block;float:none;width:520px;min-height:0;margin:auto
}
}
@media screen and (max-width: 500px){
.wraper .item{width:auto
}
}



