
.v-music{position:relative;font-family:Arial,Helvetica,sans-serif;-webkit-box-shadow:0 2px 2px 0 rgba(0,0,0,0.14),0 3px 1px -2px rgba(0,0,0,0.2),0 1px 5px 0 rgba(0,0,0,0.12);box-shadow:0 2px 2px 0 rgba(0,0,0,0.14),0 3px 1px -2px rgba(0,0,0,0.2),0 1px 5px 0 rgba(0,0,0,0.12);display:inline-block;font-size:14px;width:520px;overflow:hidden;border-top:1px solid #e9e9e9
}
.v-music .v-theme{position:absolute;top:6px;right:2px;cursor:pointer
}
.v-music .v-theme i{float:right;margin-right:3px;color:#666
}
.v-music .v-theme i:hover .theme-list{display:block
}
.v-music .v-theme .theme-list{display:none;position:absolute;left:-87px;width:100px;height:90px;background-color:white;-webkit-box-shadow:0 2px 2px 0 rgba(0,0,0,0.14),0 3px 1px -2px rgba(0,0,0,0.2),0 1px 5px 0 rgba(0,0,0,0.12);box-shadow:0 2px 2px 0 rgba(0,0,0,0.14),0 3px 1px -2px rgba(0,0,0,0.2),0 1px 5px 0 rgba(0,0,0,0.12);cursor:pointer;z-index:999
}
.v-music .v-theme .theme-list .on:before{position:absolute;content:'âˆš';font-size:20px;bottom:-5px;right:5px
}
.v-music .v-theme .theme-list .theme-item{position:relative;display:inline-block;padding:0 2px 5px 2px;width:45px;line-height:40px;font-size:12px;text-align:center
}
.v-music .v-theme .theme-list .v-theme-red{color:white;background-color:#c62f2f
}
.v-music .v-theme .theme-list .v-theme-blue{color:white;background-color:#66b7ff
}
.v-music .v-theme .theme-list .v-theme-black{color:white;background-color:#232326
}
.v-music .v-search{position:absolute;top:6px;right:25px
}
.v-music .v-search i{position:absolute;bottom:3px;left:5px;font-size:12px;color:#666
}
.v-music .v-search input{font-size:12px;outline:none;-webkit-border-radius:10px;border-radius:10px;border:1px solid rgba(0,0,0,0.2);padding-left:18px;width:100px;color:#666
}
.v-music .v-pic{position:relative;display:inline-block;width:95px;height:95px;background-size:cover
}
.v-music .v-pic i{color:white;font-size:23px;cursor:pointer
}
.v-music .v-pic i.icon-play{position:absolute;right:32%;bottom:30%;font-size:35px;-webkit-animation:showPlay 0.2s ease-out;animation:showPlay 0.2s ease-out
}
.v-music .v-pic i.icon-pause{position:absolute;right:5%;bottom:5%;-webkit-animation:showPause 0.2s ease-out;animation:showPause 0.2s ease-out
}
@-webkit-keyframes showPlay{
from{position:absolute;right:5%;bottom:5%;font-size:23px
}
to{position:absolute;right:32%;bottom:30%;font-size:35px
}
}
@keyframes showPlay{
from{position:absolute;right:5%;bottom:5%;font-size:23px
}
to{position:absolute;right:32%;bottom:30%;font-size:35px
}
}
@-webkit-keyframes showPause{
from{position:absolute;right:32%;bottom:30%;font-size:35px
}
to{position:absolute;right:5%;bottom:5%;font-size:23px
}
}
@keyframes showPause{
from{position:absolute;right:32%;bottom:30%;font-size:35px
}
to{position:absolute;right:5%;bottom:5%;font-size:23px
}
}
.v-music .v-info{float:right;width:420px;height:95px;overflow:hidden;padding:10px 7px 5px 7px;-webkit-box-sizing:border-box;box-sizing:border-box
}
.v-music .v-info .v-title{overflow:hidden;white-space:nowrap;text-overflow:ellipsis;margin:0 0 13px 5px;-webkit-user-select:text;-moz-user-select:text;-ms-user-select:text;user-select:text;cursor:default;padding-bottom:2px
}
.v-music .v-info .v-title .song{font-size:15px
}
.v-music .v-info .v-title .singer{font-size:12px;color:#666
}
.v-music .v-info .v-lyr{height:45px;background:#fff;text-align:center;overflow:hidden;margin:-10px 0 7px
}
.v-music .v-info .v-lyr .lyr-list{-webkit-transition:-webkit-transform 0.4s ease-out;transition:-webkit-transform 0.4s ease-out;transition:transform 0.4s ease-out;transition:transform 0.4s ease-out, -webkit-transform 0.4s ease-out
}
.v-music .v-info .v-lyr p{display:block;font-size:12px;line-height:15px;color:#666;opacity:.4
}
.v-music .v-info .v-lyr p.on{opacity:1
}
.v-music .v-info .v-progress{position:relative;margin:0 0 0 5px;-webkit-box-flex:1;-webkit-flex:1;-ms-flex:1;flex:1
}
.v-music .v-info .v-progress .v-bar{float:left;width:63%;height:8px;cursor:pointer
}
.v-music .v-info .v-progress .v-line{width:100%;position:relative;height:2px;background:#cdcdcd
}
.v-music .v-info .v-progress .v-line-to{position:absolute;top:0;left:0;width:0;height:2px;background:#ad7a86;-webkit-transition:all 0.05s linear;transition:all 0.05s linear
}
.v-music .v-info .v-progress .v-line-to .v-point{position:absolute;top:0;right:5px;margin-top:-5px;margin-right:-10px;height:10px;width:10px;-webkit-border-radius:50%;border-radius:50%;background:#fff;cursor:pointer;border:1px solid #ad7a86
}
.v-music .v-info .v-progress .v-line-to .v-point:hover{background-color:#ad7a86
}
.v-music .v-info .v-progress .v-des{position:relative;margin-left:63%;width:37%;right:0;bottom:10px;height:17px;color:#999;font-size:11px;padding-left:7px;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none
}
.v-music .v-info .v-progress .v-des .i-control{position:relative;display:inline-block;line-height:17px;vertical-align:middle
}
.v-music .v-info .v-progress .v-des i{opacity:0.6;cursor:pointer
}
.v-music .v-info .v-progress .v-des i:hover{opacity:1
}
.v-music .v-info .v-progress .v-des i.icon-volume-max{font-size:18px;padding-left:2px;font-size:18px
}
.v-music .v-info .volume:hover .volume-progress{display:block
}
.v-music .v-info .volume-progress{display:none;position:absolute;bottom:18px;right:9px;width:7px;height:35px;background:#aaa;cursor:pointer
}
.v-music .v-info .volume-progress .volume-to{position:absolute;bottom:0;background:#ad7a86;width:7px;-webkit-transition:all .1s ease;transition:all .1s ease
}
.v-music .show-list{overflow:hidden;-webkit-transition:all .5s ease-in-out;transition:all .5s ease-in-out;will-change:height;max-height:350px
}
.v-music .show-list p{display:block;line-height:32px;padding-left:15px
}
.v-music .show-list ol{max-height:140px;overflow-y:auto;overflow-x:hidden;-webkit-overflow-scrolling:touch
}
.v-music .show-list ol::-webkit-scrollbar{width:4px;height:8px;background-color:rgba(0,0,0,0.5);-webkit-border-radius:4px;border-radius:4px
}
.v-music .show-list ol::-webkit-scrollbar-thumb{background:#ad7a86;-webkit-border-radius:4px;border-radius:4px
}
.v-music .show-list ol li{position:relative;height:32px;line-height:32px;list-style-type:armenian;padding:0 15px;font-size:12px;border-top:1px solid #e9e9e9;cursor:pointer;-webkit-transition:all .2s ease;transition:all .2s ease;overflow:hidden
}
.v-music .show-list ol li:last-child{border-bottom:1px solid #e9e9e9
}
.v-music .show-list ol li span.v-cur{position:absolute;width:3px;height:22px;left:0;top:5px;cursor:pointer;background:#ad7a86
}
.v-music .show-list ol li span.song{margin-left:10px
}
.v-music .show-list ol li span.singer{float:right
}
.v-music .show-list ol li.on,.v-music .show-list ol li:hover{background:#efefef
}
.v-music .list-hide{max-height:0;opacity:0;visibility:hidden;-webkit-transform:translate3d(0, -20%, 0);transform:translate3d(0, -20%, 0)
}
.theme-red .show-list ol li span.v-cur{background-color:#d33030
}
.theme-red .show-list ol::-webkit-scrollbar-thumb{background:#d33030
}
.theme-red .v-info .v-progress .v-line-to{background-color:#d33030
}
.theme-red .v-info .v-progress .v-line-to .v-point{border-color:#d33030
}
.theme-red .v-info .v-progress .v-line-to .v-point:hover{background-color:#d33030
}
.theme-red .v-info .volume-progress .volume-to{background-color:#d33030
}
.theme-red .v-pic i.icon-play{color:#d33030
}
.theme-red .v-pic i.icon-pause{color:#d33030
}
.theme-blue .show-list ol li span.v-cur{background-color:#5bb8fb
}
.theme-blue .show-list ol::-webkit-scrollbar-thumb{background:#5bb8fb
}
.theme-blue .v-info .v-progress .v-line-to{background-color:#5bb8fb
}
.theme-blue .v-info .v-progress .v-line-to .v-point{border-color:#5bb8fb
}
.theme-blue .v-info .v-progress .v-line-to .v-point:hover{background-color:#5bb8fb
}
.theme-blue .v-info .volume-progress .volume-to{background-color:#5bb8fb
}
.theme-blue .v-pic i.icon-play{color:#5bb8fb
}
.theme-blue .v-pic i.icon-pause{color:#5bb8fb
}
.theme-black .show-list ol li span.v-cur{background-color:#222225
}
.theme-black .show-list ol::-webkit-scrollbar-thumb{background:#222225
}
.theme-black .v-info .v-progress .v-line-to{background-color:#222225
}
.theme-black .v-info .v-progress .v-line-to .v-point{border-color:#222225
}
.theme-black .v-info .v-progress .v-line-to .v-point:hover{background-color:#222225
}
.theme-black .v-info .volume-progress .volume-to{background-color:#222225
}
.theme-black .v-pic i.icon-play{color:white
}
.theme-black .v-pic i.icon-pause{color:white
}
@media screen and (max-width: 520px){
.v-music{margin-left:1%;width:98%
}
.v-music .v-content .v-pic{width:20%
}
.v-music .v-content .v-info{width:80%
}
.v-music .v-content .v-progress .v-bar{width:50%
}
.v-music .v-content .v-progress .v-bar .v-line-to .v-point{width:12px;height:12px;margin-top:-6px
}
.v-music .v-content .v-progress .v-des{position:absolute;margin-left:0;right:-5px;bottom:-8px;width:auto
}
.v-music .v-content .show-list ol li:hover{background-color:white
}
.v-music .v-content .show-list ol li.on:hover{background-color:#efefef
}
}
@media screen and (max-width: 375px){
.v-music .v-content .v-progress .v-bar{width:45%
}
}
@media screen and (max-width: 320px){
.v-music .v-content .v-progress .v-bar{width:40%
}
}



